import { createStore } from '@stencil/store';

interface RouterStore {
  routingKeyHistory: string[];
  routingKeyPointer: number;
  animatingPointer: number;
  initialRoutingKeyHistory: string[] | null;
  initialRoutingKeyPointer: number | null;
  // status: 'CLOSED' | 'OPENED';
}

export const routerStore = createStore<RouterStore>({
  routingKeyHistory: [],
  routingKeyPointer: -1,
  animatingPointer: -1,
  initialRoutingKeyHistory: null,
  initialRoutingKeyPointer: null
});
